/* ==========================================================================
   WooCommerce Message Responsive styles - begin
   ========================================================================== */

@media only screen and (max-width: $ipad-portrait) {

    .qodef-woocommerce-page {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 20px 25px 10px 20px!important;
        }
    }
}

@media only screen and (max-width: $phone-landscape) {

    .qodef-woocommerce-page {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 14px 20px;
            line-height: 24px;

            a {
            	float: none;
                margin: 0 70% 15px 0 !important;
                white-space: nowrap;

            }
        }
    }
}
/* ==========================================================================
   WooCommerce Message Responsive styles - end
   ========================================================================== */